import {connect} from 'react-redux'
import ImgList from '../components/ImgList'

const mapStateToProps = state=>{
    return{
        imgs:state.imgs
    }
}

let ShowImgList  = connect(
    mapStateToProps
)(ImgList)

export default ShowImgList


