import React from 'react'

class ImgList extends React.Component{
    constructor(props){
        console.log(props)
        super(props)
    }
    render(){
        return (
            <div>
                {this.props.imgs.map(img=>(
                    <img key={img.id} src={img.src} alt=""/>
                ))}
            </div>
        )
    }
}

export default ImgList


