const initState={
    imgs:[
        {
            id:1,
            src:require('../assets/imgs/mm1.jpg'),
            text:''
        }
    ]
}

const imgs = (state=initState.imgs,action)=>{
    switch(action.type){
        case 'test':
            return [
                ...state,
                {
                    id:1,
                    src:require('../assets/imgs/mm1.jpg'),
                    text:''
                }
            ]
        default:
            return state
    }
}

export default imgs


